import React, { useContext, useEffect } from 'react'
import { Box, Button, Typography, styled } from '@mui/material'
import { Helmet } from 'react-helmet-async'
import { Web3Context } from '../context'
import { APP_TITLE, PAGE_TITLE_LOGIN } from '../utils/constants'
import { useNavigate } from 'react-router-dom'
import WeehodlABI from '../abis/weehodl.json'

export const Login = () => {
  const navigate = useNavigate()
  const { account, connect, web3 } = useContext(Web3Context)
  const handleLogin = async () => {
    await connect()
  }
  useEffect(() => {
    const permissionWrapper = async () => {
      if (web3 && account) {
        const contract = new web3.eth.Contract(
          WeehodlABI as any,
          '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902'
        )
        const owner = await contract.methods['getOwner']().call({
          from: account,
        })
        if (
          web3.utils.toChecksumAddress(account) ===
          web3.utils.toChecksumAddress(owner)
        ) {
          navigate('/admin')
        } else {
          navigate('/')
        }
      }
    }
    permissionWrapper()
  }, [account, navigate, web3])
  return (
    <Box display="grid" height="100vh">
      <Helmet>
        <title>
          {PAGE_TITLE_LOGIN} | {APP_TITLE}
        </title>
      </Helmet>
      <Box margin="auto" textAlign="center">
        <ConnectWalletButton onClick={handleLogin}>
          <Typography variant="h2">Connect Wallet</Typography>
        </ConnectWalletButton>
      </Box>
    </Box>
  )
}

const ConnectWalletButton = styled(Button)`
  border-radius: 40px;
  border-color: #212121;
  padding: 30px 50px;
  &:hover {
    background-color: #c0c0c0;
  }
`
