import {
  Box,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
  Paper,
} from '@mui/material'
import BigNumber from 'bignumber.js'
import { PageWrapper } from './Components'
import { useContext, useEffect, useState } from 'react'
import { Web3Context } from '../context'
import WeehodlABI from '../abis/weehodl.json'
import ERC20ABI from '../abis/erc20.json'

export const Home = () => {
  const [projects, setProjects] = useState<Array<any>>([])
  const [currentProject, setCurrentProject] = useState<any>(null)
  const [investAmount, setInvestAmount] = useState<string>('')
  const { account, web3 } = useContext(Web3Context)

  useEffect(() => {
    const projectWrapper = async () => {
      if (web3 && account) {
        const contract = new web3.eth.Contract(
          WeehodlABI as any,
          '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902'
        )
        const projectNums = await contract.methods['g_numTokens']().call({
          from: account,
        })
        const projects = []
        for (let index = 0; index < projectNums; index++) {
          projects.push(
            await contract.methods['g_projects'](index).call({ from: account })
          )
        }
        console.log(projects)
        setProjects(projects)
      }
    }
    projectWrapper()
  }, [web3, account])

  const handleInvest = async () => {
    if (web3 !== null) {
      const erc20 = new web3.eth.Contract(
        ERC20ABI as any,
        '0xe9852a19b7E15993d99a51099eb3f8DAC4f51997'
      )
      await erc20.methods['approve'](
        '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902',
        BigNumber(investAmount).times(BigNumber(10).pow(18)).toString()
      ).send({ from: account })
      const contract = new web3.eth.Contract(
        WeehodlABI as any,
        '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902'
      )
      console.log(currentProject)
      await contract.methods['invest'](
        currentProject.poolId,
        BigNumber(investAmount).times(BigNumber(10).pow(18)).toString()
      ).send({ from: account })
    }
  }
  const handleClaim = async () => {
    if (web3 !== null) {
      const contract = new web3.eth.Contract(
        WeehodlABI as any,
        '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902'
      )
      await contract.methods['claim'](currentProject.poolId).send({
        from: account,
      })
    }
  }
  return (
    <PageWrapper>
      <Box width="100%" display="flex" height="100%">
        <TableContainer
          component={Paper}
          sx={{ marginRight: '20px', width: '60%' }}
        >
          <Table aria-label="simple table" stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell align="left">Project Id</TableCell>
                <TableCell align="left">Name</TableCell>
                <TableCell align="left">Sale Status</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {projects.map((row, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  onClick={async () => {
                    if (web3 !== null) {
                      const contract = new web3.eth.Contract(
                        WeehodlABI as any,
                        '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902'
                      )
                      const investNums = await contract.methods[
                        'getProjects'
                      ]().call({ from: account })
                      setCurrentProject({
                        isOpened: row.isOpened,
                        invest: 0,
                        poolId: row.id,
                      })
                      for (let index = 0; index < investNums; index++) {
                        const pro = await contract.methods['g_investors'](
                          account,
                          index
                        ).call({ from: account })
                        if (pro.poolId === row.id)
                          setCurrentProject({ ...pro, isOpened: row.isOpened })
                      }
                    }
                  }}
                  hover
                >
                  <TableCell component="th" scope="row" align="left">
                    {row.id}
                  </TableCell>
                  <TableCell align="left">{row.projectName}</TableCell>
                  <TableCell align="left">
                    {row.isOpened ? 'opened' : 'coming...'}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <Box component={Paper} width="40%" px={4} py={4}>
          {currentProject !== null && (
            <>
              {!currentProject.isOpened && (
                <>
                  <Box
                    display="flex"
                    alignItems="end"
                    justifyContent="center"
                    mb={4}
                  >
                    <Typography variant="h4" color="blue">
                      {BigNumber(currentProject.invest)
                        .dividedBy(BigNumber(10).pow(18))
                        .toString()}
                    </Typography>
                    <Typography ml={2}>invested</Typography>
                  </Box>
                  <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                  >
                    <TextField
                      variant="outlined"
                      type="number"
                      value={investAmount}
                      onChange={(ev) => setInvestAmount(ev.target.value)}
                    />
                    <Button sx={{ marginLeft: 2 }} onClick={handleInvest}>
                      Invest
                    </Button>
                  </Box>
                </>
              )}
              {currentProject.isOpened && (
                <Box>
                  <Box mr={3}>
                    token1:
                    <Typography variant="h4" mr={1} color="red">
                      {BigNumber(currentProject.claimedAmount1 || 0)
                        .dividedBy(BigNumber(10).pow(18))
                        .toString()}
                    </Typography>
                    token2:
                    <Typography variant="h4" mr={1} color="red">
                      {BigNumber(currentProject.claimedAmount2 || 0)
                        .dividedBy(BigNumber(10).pow(18))
                        .toString()}
                    </Typography>
                    {/* <Typography variant="h4" mr={1} ml={2} color="green">
                    $356
                  </Typography>
                  <Typography>claimable</Typography> */}
                  </Box>
                  <Button sx={{ marginLeft: 2 }} onClick={handleClaim}>
                    Claim
                  </Button>
                </Box>
              )}
            </>
          )}
        </Box>
      </Box>
    </PageWrapper>
  )
}
