import React, { useContext, useState } from 'react'
import { Box, Button, Menu, MenuItem, Typography, styled } from '@mui/material'
import { ArrowDropDown, Wallet } from '@mui/icons-material'
import { Web3Context } from '../../context'

export const WalletButton = () => {
  const { account, disconnect, connect } = useContext(Web3Context)
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)
  const open = Boolean(anchorEl)
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget)
  }
  const handleClose = (event?: string) => {
    setAnchorEl(null)
    if (event === 'disconnect') {
      disconnect()
    }
  }
  return (
    <Box>
      {account === null ? (
        <ConnectButton onClick={() => connect()}>
          <Wallet />
          <Typography ml={1} variant="h6">
            Connect Wallet
          </Typography>
        </ConnectButton>
      ) : (
        <>
          <ConnectButton
            aria-controls={'account_menu'}
            aria-haspopup="true"
            aria-expanded="true"
            onClick={handleClick}
          >
            <Wallet />
            <Typography ml={1} variant="h6">
              {account?.slice(0, 5) + '...' + account?.slice(-3)}
            </Typography>
            <ArrowDropDown />
          </ConnectButton>
          <Menu
            id="account-menu"
            anchorEl={anchorEl}
            open={open}
            onClose={() => handleClose()}
            anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
            MenuListProps={{
              'aria-labelledby': 'basic-button',
            }}
          >
            <MenuItem
              onClick={() => handleClose('disconnect')}
              value="disconnect"
            >
              <Typography variant="h6" width="fit-content">
                Disconnect
              </Typography>
            </MenuItem>
          </Menu>
        </>
      )}
    </Box>
  )
}

const ConnectButton = styled(Button)`
  flex: none;
  border-radius: 20px;
  padding: 4px 8px 4px 16px;
  text-transform: none;
  &:hover {
    background-color: #111111;
  }
`
