import {
  Paper,
  Box,
  Button,
  FormLabel,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Typography,
  TextField,
} from '@mui/material'
import { PageWrapper } from './Components'
import { useContext, useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { Web3Context } from '../context'
import WeehodlABI from '../abis/weehodl.json'

export const ProjectDetail = () => {
  const navigate = useNavigate()
  const { pool_id } = useParams()
  const { web3, account } = useContext(Web3Context)
  const [invests, setInvests] = useState<Array<any>>([])
  const [contract, setContract] = useState<any>(null)
  const [project, setProject] = useState<any>({})
  const param = {
    address: '0x0000000000000000000000000000000000000000',
    price: '0',
    percent: '0',
  }
  const [token1, setToken1] = useState<any>(param)
  const [token2, setToken2] = useState<any>(param)
  useEffect(() => {
    const projectWrapper = async () => {
      setProject(
        await contract.methods['g_projects'](pool_id).call({ from: account })
      )
      const investors = await contract.methods['getInvestors'](pool_id).call({
        from: account,
      })
      setInvests(investors)
    }
    if (contract !== null) projectWrapper()
  }, [pool_id, account, contract])
  useEffect(() => {
    if (web3 !== null) {
      setContract(
        new web3.eth.Contract(
          WeehodlABI as any,
          '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902'
        )
      )
    }
  }, [web3])
  const handleSubmit = async () => {
    let params: Array<any> = []
    if (
      project !== null &&
      project.isOpened &&
      project.vestingPercent2 !== '0'
    ) {
      params = [
        pool_id,
        project.firstTokenAddr,
        project.salePrice1,
        token1.percent,
        project.secondTokenAddr,
        project.salePrice2,
        token2.percent,
      ]
    }
    if (
      project !== null &&
      project.isOpened &&
      project.vestingPercent2 === '0'
    ) {
      params = [
        pool_id,
        project.firstTokenAddr,
        project.salePrice1,
        token1.percent,
        token2.address,
        token2.price,
        token2.percent,
      ]
    }
    if (project === null || !project.isOpened) {
      params = [
        pool_id,
        token1.address,
        token1.price,
        token1.percent,
        token2.address,
        token2.price,
        token2.percent,
      ]
    }
    console.log(params)
    contract.methods['setInfoForProject'](...params).send({ from: account })
  }
  return (
    <PageWrapper>
      <Box display="flex" justifyContent="end" width="100%">
        <Button
          sx={{ textTransform: 'none' }}
          onClick={() => navigate('/admin')}
        >
          Back
        </Button>
      </Box>
      <Box component={Paper} px={4} py={2} my={2} width="100%">
        <Box
          width="100%"
          display="flex"
          alignItems="center"
          flexDirection="column"
          gap={4}
        >
          <Box
            width="100%"
            display="flex"
            alignItems="center"
            justifyContent="center"
          >
            <Box display="flex" flexDirection="column" mr={8} gap={1}>
              <FormLabel>Token 1</FormLabel>
              {project.isOpened ? (
                <Typography>{project.firstTokenAddr}</Typography>
              ) : (
                <TextField
                  id="address"
                  variant="standard"
                  placeholder="0x00000000000000000000000000000000"
                  value={token1.address}
                  sx={{ minWidth: '390px' }}
                  onChange={(ev) =>
                    setToken1({ ...token1, address: ev.target.value })
                  }
                />
              )}

              <Box
                display="flex"
                alignItems="center"
                justifyContent="space-between"
              >
                <FormLabel>Price</FormLabel>
                {project.isOpened ? (
                  <Typography>{project.salePrice1}</Typography>
                ) : (
                  <TextField
                    id="price"
                    type="number"
                    variant="standard"
                    prefix="$"
                    sx={{ width: '80px', marginLeft: '8px' }}
                    placeholder="$5.00"
                    value={token1.price}
                    disabled={project.open}
                    onChange={(ev) =>
                      setToken1({ ...token1, price: Number(ev.target.value) })
                    }
                  />
                )}
              </Box>
              <Box
                display="flex"
                alignItems="center"
                justifyContent="space-between"
              >
                <FormLabel>Percent</FormLabel>
                {project.isOpened && (
                  <Typography mr={1}>{project.vestingPercent1}</Typography>
                )}
                <TextField
                  id="percent"
                  type="number"
                  variant="standard"
                  sx={{ width: '80px', marginLeft: '8px' }}
                  prefix="%"
                  placeholder="%6.7"
                  value={token1.percent}
                  onChange={(ev) =>
                    setToken1({ ...token1, percent: Number(ev.target.value) })
                  }
                />
              </Box>
            </Box>
            <Box display="flex" flexDirection="column" gap={1}>
              <FormLabel>Token 2</FormLabel>
              {project.isOpened &&
              project.secondTokenAddr !==
                '0x0000000000000000000000000000000000000000' ? (
                <Typography>{project.vestingPercent1}</Typography>
              ) : (
                <TextField
                  id="address"
                  variant="standard"
                  placeholder="0x00000000000000000000000000000000"
                  value={token2.address}
                  sx={{ minWidth: '390px' }}
                  onChange={(ev) =>
                    setToken2({ ...token2, address: ev.target.value })
                  }
                />
              )}

              <Box
                display="flex"
                alignItems="center"
                justifyContent="space-between"
              >
                <FormLabel>Price</FormLabel>
                {project.isOpened && project.salePrice2 !== '0' ? (
                  <Typography>{project.salePrice2}</Typography>
                ) : (
                  <TextField
                    id="price"
                    type="number"
                    variant="standard"
                    prefix="$"
                    sx={{ width: '80px', marginLeft: '8px' }}
                    placeholder="$5.00"
                    value={token2.price}
                    onChange={(ev) =>
                      setToken2({ ...token2, price: Number(ev.target.value) })
                    }
                  />
                )}
              </Box>
              <Box
                display="flex"
                alignItems="center"
                justifyContent="space-between"
              >
                <FormLabel>Percent</FormLabel>
                {project.isOpened && project.vestingPercent2 !== '0' && (
                  <Typography mr={1}>{project.vestingPercent2}</Typography>
                )}
                <TextField
                  id="percent"
                  type="number"
                  variant="standard"
                  sx={{ width: '80px', marginLeft: '8px' }}
                  prefix="%"
                  placeholder="%6.7"
                  value={token2.percent}
                  onChange={(ev) =>
                    setToken2({ ...token2, percent: Number(ev.target.value) })
                  }
                />
              </Box>
            </Box>
          </Box>
          <Button onClick={handleSubmit}>Apply</Button>
        </Box>
      </Box>
      <TableContainer component={Paper} sx={{ height: '100%' }}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table" stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell align="left">Id</TableCell>
              <TableCell align="left">Address</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {invests.map((row, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                hover
              >
                <TableCell component="th" scope="row" align="left">
                  {index + 1}
                </TableCell>
                <TableCell align="left">{row}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </PageWrapper>
  )
}
