import { Route } from '../types/Route'
import { Home } from '../pages/Home'
import { Login } from '../pages/Login'
import { Admin } from '../pages/Admin'
import { NewProject } from '../pages/NewProject'
import { ProjectDetail } from '../pages/ProjectDetail'

export const routes: Array<Route> = [
  {
    key: 'router-home',
    title: 'Home',
    description: 'Home',
    component: Home,
    isEnabled: true,
    path: '/',
  },
  {
    key: 'router-admin-home',
    title: 'Home',
    description: 'Admin Home',
    component: Admin,
    isEnabled: true,
    path: '/admin',
  },
  {
    key: 'router-admin-login',
    title: 'Login',
    description: 'Admin Login',
    component: Login,
    isEnabled: true,
    path: '/login',
  },
  {
    key: 'router-admin-new-project',
    title: 'NewProject',
    description: 'NewProject',
    component: NewProject,
    isEnabled: true,
    path: '/admin/new_project',
  },
  {
    key: 'router-admin-project-detail',
    title: 'ProjectDetail',
    description: 'ProjectDetail',
    component: ProjectDetail,
    isEnabled: true,
    path: '/admin/project/:pool_id',
  },
]
