import {
  Box,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
} from '@mui/material'
import { useEffect, useState, useContext } from 'react'
import { PageWrapper } from './Components'
import { useNavigate } from 'react-router-dom'
import { Add } from '@mui/icons-material'
import WeehodlABI from '../abis/weehodl.json'
import ERC20 from '../abis/erc20.json'
import BigNumber from 'bignumber.js'
import { Web3Context } from '../context'

export const Admin = () => {
  const [projects, setProjects] = useState<Array<any>>([])
  const { web3, account } = useContext(Web3Context)
  const [fundAddr, setFundAddr] = useState<string>('')
  const [fundAmount, setFundAmount] = useState<number>(0)
  const navigate = useNavigate()
  useEffect(() => {
    const projectWrapper = async () => {
      if (web3 && account) {
        const contract = new web3.eth.Contract(
          WeehodlABI as any,
          '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902'
        )
        const projectNums = await contract.methods['g_numTokens']().call({
          from: account,
        })
        const projects = []
        for (let index = 0; index < projectNums; index++) {
          projects.push(
            await contract.methods['g_projects'](index).call({ from: account })
          )
        }
        console.log(projects)
        setProjects(projects)
      }
    }
    projectWrapper()
  }, [web3, account])
  const handleClickProject = (pool_id: number) => {
    navigate(`/admin/project/${pool_id}`)
  }
  const handleFunds = async () => {
    if (web3 && account) {
      const erc20 = new web3.eth.Contract(ERC20 as any, fundAddr)
      const decimals = await erc20.methods['decimals']().call({ from: account })
      await erc20.methods['approve'](
        '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902',
        BigNumber(fundAmount).times(BigNumber(10).pow(decimals)).toString()
      ).send({ from: account })
      const contract = new web3.eth.Contract(
        WeehodlABI as any,
        '0x63f35c6ecf8acd0CDB292ff7bbfcF2Cc723e1902'
      )
      contract.methods['fundTokens'](
        fundAddr,
        BigNumber(fundAmount).times(BigNumber(10).pow(decimals)).toString()
      ).send({ from: account })
    }
  }
  return (
    <PageWrapper>
      <Box mb={2} display="flex" justifyContent={'end'} width="100%">
        <Button
          sx={{ textTransform: 'none' }}
          onClick={() => navigate('/admin/new_project')}
        >
          <Add />
          New Project
        </Button>
      </Box>
      <Box component={Paper} sx={{ width: '100%', height: '100%' }}>
        <Box my={3} mx={2} display="flex" alignItems="center" gap={2}>
          <TextField
            value={fundAddr}
            onChange={(ev) => setFundAddr(ev.target.value)}
          />
          <TextField
            type="number"
            value={fundAmount}
            onChange={(ev) => setFundAmount(Number(ev.target.value))}
          />
          <Button onClick={handleFunds}>Fund</Button>
        </Box>
        <TableContainer sx={{ width: '100%', minHeight: '100%' }}>
          <Table aria-label="simple table" stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell align="left">Pool Id</TableCell>
                <TableCell align="left">Name</TableCell>
                <TableCell align="left">Total Invested</TableCell>
                <TableCell align="left">Sale Status</TableCell>
                <TableCell align="left">Token 1</TableCell>
                <TableCell align="left">Token 2</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {projects.map((project, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  onClick={() => handleClickProject(project.id)}
                  hover
                >
                  <TableCell component="th" scope="row" align="left">
                    {project.id}
                  </TableCell>
                  <TableCell align="left">{project.projectName}</TableCell>
                  <TableCell align="left">{project.totalInvested}</TableCell>
                  <TableCell align="left">
                    {project.isOpened ? 'opened' : 'coming...'}
                  </TableCell>
                  <TableCell align="left">
                    {project.firstTokenAddr ===
                    '0x0000000000000000000000000000000000000000'
                      ? ''
                      : project.firstTokenAddr}
                  </TableCell>
                  <TableCell align="left">
                    {project.secondTokenAddr ===
                    '0x0000000000000000000000000000000000000000'
                      ? ''
                      : project.secondTokenAddr}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Box>
    </PageWrapper>
  )
}
